<script>
export let MSWPromptRaw = '';
export let MSWPromptMassage = '';
export let MSWPromptOptions = {};

let _OLSKLocalizedMap = JSON.parse(`{"OLSK_I18N_SEARCH_REPLACE":"OLSK_I18N_SEARCH_REPLACE"}`);
import OLSKInternational from 'OLSKInternational';
const OLSKLocalized = function(inputData) {
	return OLSKInternational.OLSKInternationalLocalizedString(inputData, _OLSKLocalizedMap[MSWPromptOptions.MSWOptionLanguage || 'en']);
};

import MassageTXT from 'MassageTXT';
import MSWPromptLogic from './ui-logic.js';

import unifiedPackage from 'unified';
import remarkParsePackage from 'remark-parse';
const uParser = unifiedPackage().use(remarkParsePackage).parse;

const mod = {

	// VALUE

	_ValueTraceObjects: [],

	_ValueOutput: '',

	// MESSAGE

	MSWEditorDispatchValueDidChangeRaw (inputData) {
		MSWPromptRaw = inputData;

		mod.ReactOutput();
	},

	MSWEditorDispatchValueDidChangeMassage (inputData) {
		MSWPromptMassage = inputData;

		mod.ReactOutput();
	},

	// REACT

	ReactOutput () {
		const item = [];
		delete mod._ValueSyntaxError;

		try {
			mod._ValueOutput = MSWPromptMassage[0] !== '$' ? MSWPromptRaw : MassageTXT.MSTMassage(MSWPromptRaw, MSWPromptMassage.replace(/\\n/g, '\n'), {
				MSTOptionMarkdownParser: uParser,
				MSTOptionTrace (index, key, value) {
					if (!item[index]) {
						item[index] = {}
					}

					item[index][key] = value;
				},
			});
		} catch (err) {
			mod._ValueSyntaxError = err;
		}

		mod._ValueTraceObjects = item.slice(1);
	},

	// SETUP

	SetupEverything() {
		mod.ReactOutput();
	},

	// LIFECYCLE

	LifecycleModuleWillMount() {
		mod.SetupEverything();
	},

};

mod.LifecycleModuleWillMount();

import MSWEditor from './submodules/MSWEditor/main.svelte';
import MSWPromptTraceItem from './submodules/MSWPromptTraceItem/main.svelte';
</script>

<div class="MSWPrompt">

<div class="MSWPromptToolbar">
	<a class="MSWPromptPermalink" href={ `${ window.location.pathname }?raw=${ MSWPromptLogic.MSWPermalinkEncode(MSWPromptRaw) }&massage=${ MSWPromptLogic.MSWPermalinkEncode(MSWPromptMassage) }` } target="_blank">{ OLSKLocalized('MSWPromptPermalinkText') }</a>
</div>

<div class="MSWPromptWorkspace">
	<div class="MSWPromptConvert">
		<div class="MSWPromptRaw">
			<MSWEditor MSWEditorOptions={ {
				lineNumbers: true,
				lineWrapping: true,

				keyMap: 'sublime',

				extraKeys: {
					Tab: false,
				},
			} } MSWEditorInitialValue={ MSWPromptRaw } MSWEditorDispatchValueDidChange={ mod.MSWEditorDispatchValueDidChangeRaw } />
		</div>

		<div class="MSWPromptTrace">
			{#each mod._ValueTraceObjects as item}
				<MSWPromptTraceItem MSWPromptTraceItemOperation={ item.MSTTraceOperation } MSWPromptTraceItemInput={ MSWPromptLogic.MSWStringify(MSWPromptLogic.MSWVisualString(item.MSTTraceInput)) } MSWPromptTraceItemArguments={ (item.MSTTraceArguments || []).filter(function (e) { return typeof e !== 'function'; }).map(function (e) {
					return MSWPromptLogic.MSWStringify(MSWPromptLogic.MSWVisualString(e))
				}) } OLSKLocalized={ OLSKLocalized } />
			{/each}

			{#if mod._ValueSyntaxError}
				<div class="MSWPromptSyntaxError">{ mod._ValueSyntaxError.message }</div>
			{/if}
		</div>

		<pre class="MSWPromptOutput">{ mod._ValueOutput }</pre>
	</div>

	<div class="MSWPromptMassage">
		<MSWEditor MSWEditorOptions={ {
			lineWrapping: true,

			keyMap: 'sublime',

			extraKeys: {
				Tab: false,
				'Shift-Tab': false,
			},
		} } MSWEditorInitialValue={ MSWPromptMassage } MSWEditorDispatchValueDidChange={ mod.MSWEditorDispatchValueDidChangeMassage } />
	</div>
</div>

</div>

<style src="./ui-style.css">:root {
	--MSWPromptFontSize: 10pt;
	--MSWPromptPadding: 10px;
}

.MSWPrompt {
	height: 100%;

	font-family: 'Helvetica Neue', 'Helvetica', sans;
	font-size: var(--MSWPromptFontSize);

	/* MSWPromptFlexbox:Parent */
	display: flex;
	flex-direction: column;
}

.MSWPromptToolbar {
	padding: var(--MSWPromptPadding);

	background: rgba(0, 0, 0, 6%);

	/* MSWPromptToolbar:Parent */
	display: flex;
	justify-content: flex-end;
}

.MSWPromptWorkspace {
	/* MSWPromptFlexbox:Child */
	flex-grow: 1;

	/* MSWPromptWorkspaceFlexbox:Parent */
	display: flex;
	flex-direction: column;
}

.MSWPromptConvert {
	/* MSWPromptWorkspaceFlexbox:Child */
	flex-grow: 1;

	/* MSWPromptConvertFlexbox:Parent */
	display: flex;
}

.MSWPromptConvert > * {
	/* MSWPromptConvertFlexbox:Child */
	flex-basis: 40%;
}

.MSWPromptRaw {
	/* MSWPromptRawStation:Parent */
	position: relative;
}

.MSWPromptRaw :global(.CodeMirror) {
	font-size: var(--MSWPromptFontSize);
	
	/* MSWPromptRawStation:Child */
	position: absolute;
	top: 0;
	right: 0;
	left: 0;
	bottom: 0;

	/* override defaults */
	height: 100%;
}

.MSWPromptTrace {
	padding: var(--MSWPromptPadding);

	background: rgba(0, 0, 0, 2%);

	/* MSWPromptConvertFlexbox:Child */
	flex-basis: 20% !important;

	/* cap width */
	overflow: scroll;
	overflow-wrap: break-word;
}

.MSWPromptOutput {
	margin: 0;
	padding: var(--MSWPromptPadding);

	background: rgba(0, 0, 0, 4%);

	/* cap width */
	overflow: scroll;
	overflow-wrap: break-word;
}

.MSWPromptMassage {
	border-top: 1px solid rgba(0, 0, 0, 0.3);

	/* MSWPromptWorkspaceFlexbox:Child */
	min-height: 80px;

	/* MSWPromptMassageStation:Parent */
	position: relative;
}

.MSWPromptMassage :global(.CodeMirror) {
	font-size: var(--MSWPromptFontSize);

	/* MSWPromptMassageStation:Child */
	position: absolute;
	top: 0;
	right: 0;
	left: 0;
	bottom: 0;

	/* override defaults */
	height: 100%;
}

.MSWPromptMassage :global(.CodeMirror-scroll) {
}

.MSWPromptMassage :global(.CodeMirror-lines) {
	/* override defaults */
	padding: var(--MSWPromptPadding);
}

.MSWPromptMassage :global(.CodeMirror pre) {
	/* override defaults */
	padding: 0;
}
</style>
